import { useState } from "react";
import { TextField, Button } from "@mui/material";


import "../style.css";

const ChatBubble = (props) => {
  return (
    <div className={`chat-bubble ${props.position}`}>
      {props.text}
    </div>
  );
}

const Chat = () => {

  const [chatData, setChatData] = useState([
    {
      position: "chat-left",
      text: "Dobro doli",
    },
    {
      position: "chat-right",
      text: "Imam problem sa uplatom kredita",
    },
    {
      position: "chat-left",
      text: "Kredit e biti dostupan nakon proknjiene uplate. Ukoliko je prolo vie od dva radna dana od uplate, poaljite nam podatke o uplati kako bi proverili njen status",
    },
    {
      position: "chat-right",
      text: "Podaci o uplati su sledei",
    },
    {
      position: "chat-right",
      text: "Broj rauna: 0080XXXXXXX213",
    },
  ])

  const [text, setText] = useState("");

  const handleSend = () => {
    const chatDataCopy = [...chatData];
    chatDataCopy.push(
      {
        position: "chat-right",
        text,
      },
      {
        position: "chat-left",
        text: "Tehnika podrka nije dostupna. Molimo Vas, pokuajte kasnije",
      },
    )
    setChatData(chatDataCopy);
    setText("");
  }

  return (
    <div className="root">
      <h3> Razgovor sa tehnikom podrkom </h3>

      {chatData.map((item, index) => 
        <ChatBubble key={index} position={item.position} text={item.text} />
      )}

      <div className="force-margin">
        <TextField 
          fullWidth
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="Upiite poruku"
        />
        <Button
          fullWidth
          variant="contained"
          onClick={handleSend}
        > Poalji </Button>
      </div>
    </div>
  )
}

export default Chat;
